import React, { Component } from 'react';
import { Menu, Icon } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {setHeadTitle} from './../../redux/action';

const { SubMenu } = Menu;

class NavMenu extends Component {
  static propTypes = {
    menuList: PropTypes.array.isRequired,
    collapsed: PropTypes.bool.isRequired
  }
  state = {
    openKeys: [],
    selectedKeys: []
  }
  //  pathname
  getOpenKeys = string => {
    let newStr = '',
      newArr = [],
      arr = string.split('/').map(i => '/' + i)
    for (let i = 1; i < arr.length - 1; i++) {
      newStr += arr[i]
      newArr.push(newStr)
    }
    return newArr; //  /graph/bar => /graph
  }

  //  menu 
  componentDidMount() {
    let { pathname } = this.props.location
    // 
    /**
     *  /pro
     *  /pro/XXXX
     *   /pro/XXXX/yyyy
     */
    if (this.getOpenKeys(pathname).length > 1) {
      this.setState({
        selectedKeys: this.getOpenKeys(pathname)[1],
        openKeys: this.getOpenKeys(pathname)[0]
      })
    } else {
      this.setState({
        selectedKeys: [pathname],
        openKeys: this.getOpenKeys(pathname)
      })
    }

  }
  //  SubMenu
  onOpenChange = openKeys => {
    console.log('openChange');
    if (openKeys.length === 0 || openKeys.length === 1) {
      this.setState({
        openKeys
      })
      console.log('openChange-if', openKeys)
      return
    }

    //  SubMenu
    const latestOpenKey = openKeys[openKeys.length - 1]

    // 
    if (latestOpenKey.includes(openKeys[0])) {
      this.setState({
        openKeys
      })
    } else {
      this.setState({
        openKeys: [latestOpenKey]
      })
    }
  }
  /**
   * menu
   */
  getMenuNodes = menuList => {
    return menuList.map(item => {
      if (this.hasAuth(item)) { // item
        if (!item.children) {
          return (
            <Menu.Item key={item.key}>
              <Link to={item.key} onClick={() => { this.props.setHeadTitle(item.title) } }><Icon type={item.icon} /><span>{item.title}</span></Link>
            </Menu.Item>
          )
        } else {
          return (
            <SubMenu
              key={item.key}
              title={
                <span>
                  <Icon type={item.icon} />
                  <span>{item.title}</span>
                </span>
              }
            >
              {this.getMenuNodes(item.children)}
            </SubMenu>
          )
        }
      }
    })
  }
  /**
   * reduce
  */

  getMenuNodes_2 = (menuList) => {
    const path = this.props.location.pathname;
    return menuList.reduce((pre, item) => {
      // item
      if (this.hasAuth(item)) {
        // pre Menu.item
        if (!item.children) {
          pre.push((
            <Menu.Item key={item.key}>
              <Link to={item.key}><Icon type={item.icon} />{item.title}</Link>
            </Menu.Item>
          ))
        } else {
          // itemitemkey
          const cItem = item.children.find(cItem => cItem.key === path);
          // item
          // this 
          if (cItem) {
            this.setState({
              // defaultOpenKeys: [item.key]
            })
          }
          pre.push((
            <SubMenu
              key={item.key}
              title={
                <span>
                  <Icon type={item.icon} />
                  <span>{item.title}</span>
                </span>
              }
            >
              {this.getMenuNodes_2(item.children)}
            </SubMenu>
          ))
        }
      }
      return pre;
    }, []); //  []
  }

  // item
  hasAuth = (item) => {
    /**
     * 1admin
     * 2item
     * 3itemkeymenus
     */
    const { key, isPublic } = item;
    const user = JSON.parse(React.$storage_utils.getUser());
    const menus = user.role.menus;
    const username = user.username;
    if (username === 'admin' || isPublic || menus.indexOf(key) !== -1) {
      return true;
    } else if(item.children) { // itemitem
      return !!item.children.find(child => menus.indexOf(child.key) !== -1);
    } else {
      return false;
    }
  }


  render() {
    // (withRouter)
    const { menuList, collapsed } = this.props;
    let { openKeys, selectedKeys } = this.state;
    const defaultProps = collapsed ? [] : openKeys; // menu
    return (
      <div style={{ width: '100%' }}>
        <Menu
          mode="inline"
          theme='dark'
          selectedKeys={selectedKeys}
          onClick={({ key }) => this.setState({ selectedKeys: [key] })}
          onOpenChange={this.onOpenChange}
          openKeys={defaultProps}
        >
          {this.getMenuNodes(menuList)}
        </Menu>
      </div>
    );
  }
}

/**
 * withRouter
 *  
 *  3history/location/match
 *  
 */

// export default NavMenu;
// export default withRouter(NavMenu);
export default connect(
  state => ({}),
  {setHeadTitle}
)(withRouter(NavMenu));
