import React, { Component } from 'react';
import { Input, Form } from 'antd';
import './../../assets/css/category.less'
import PropType from 'prop-types';
const Item = Form.Item; // constimport

class UpdateCategory extends Component {
  static propType = {
    categoryName: PropType.string.isRequired,
    setForm: PropType.func.isRequired
  }
  componentWillMount() {
    // formsetForm()
    this.props.setForm(this.props.form);
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const {categoryName} = this.props;
    return (
      <div className="edit-category">
        <Form>
          <Item label="" labelCol={{ span: 4 }} wrapperCol={{ span: 12 }}>
            {getFieldDecorator('categoryName', {
              rules: [
                { required: true, message: '' },
                { min: 4, message: '4' },
                { max: 12, message: '12' },
              ],
              initialValue:categoryName
            })(<Input placeholder="" />)}
          </Item>
        </Form>
      </div>
    );
  }
}

// export default AddCategory;
/**
 * FormForm(Login)
 * Formform
 */
const update_category = Form.create()(UpdateCategory);
export default update_category;