import React, { Component } from 'react';
import { Table ,Card,Button,Input } from 'antd';
import {importsExcel,exportExcel} from './../../utils/excel';
import './../../assets/css/excel.less';
class ExcelOut extends Component {
    state = {
        lists:[{
            username: 'Jacqui',
            age: 32,
            sex: '',
            phone: '15617990221',
            id: 0,
        },{
            age: 20,
            id: 1,
            phone: 15617990221,
            sex: "",
            username: "Jack",
        }],
        excelIn_flag: false,
        loading: false,
        excelName: ``, // excel
    }
    componentWillMount() {
        this.initForm();
    }
    // 
    excelIn = (e) => {
        e.persist(); // react   e.target     setState (setStateevent)
        let excelName = e.currentTarget.files[0].name;
        importsExcel(e).then((data) => {
         this.setState({loading:true});          
          this.setState({
              lists: data,
              excelIn_flag: true,
              excelName,
          })    
          setInterval(() => {
            this.setState({loading:false});
          },1000)
        },(err) => {
            console.log('',err);
        })
    }
    // 
    downloadExcel = () => {
        let {lists} = this.state;
        //           
        exportExcel(this.columns,lists,".xlsx");
    }
    initForm = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'id',
                key: 'id',
                render: text => <a>{text}</a>,
            },
            {
                title: '',
                dataIndex: 'username',
                key: 'username',
            },
            {
                title: '',
                dataIndex: 'age',
                key: 'age',
            },
            {
                title: '',
                dataIndex: 'sex',
                key: 'sex',
            },
            {
                title: '',
                dataIndex: 'phone',
                key: 'phone',
            }
        ];
    }
    render() {
        let {lists,excelIn_flag,loading,excelName} = this.state;
        const cardTit = (
            <div>
                <Button type="primary" style={{marginRight: 20}} className="uploadFile">
                    <Input type="file" accept=".xls,.xlsx" onChange={(e) => this.excelIn(e) }  /> 
                    {excelIn_flag ? `${excelName}` : 'Excel'}
                </Button>
                <Button type="primary" onClick={() => this.downloadExcel()}>Excel</Button>
            </div>
        )
        return (
            <Card type="inner" className="product-index" title={cardTit}>
                <Table 
                    columns={this.columns} 
                    dataSource={lists} 
                    rowKey="id"
                    loading={loading} 
                />
            </Card>
           
        );
    }
}

export default ExcelOut;