import React, { Component } from 'react';
import { Card, Button, Table, message, Modal } from 'antd';
import { reqRoleList, reqAddRole,reqUpdateRole } from './../../api';
import { pagations } from './../../utils/tablePagination';
import AddRole from './AddRole';
import RoleAuth from './RoleAuth'
import moment from 'moment';
class Role extends Component {
  constructor(props) {
    super(props);
    /**
     * RefsReact.creactRef()
     * refreact
     * Refs:this.xxx.current
     */
    this.menuKeys = React.createRef();

  }
  state = {
    roleList: [], // 
    loading: false,
    isShowAdd: false, // 
    isShowAuth: false,
    role: {}, // role
  }
  /**
   * 
   */
  initColunms = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'create_time',
        render:(create_time)=> moment(create_time).format('YYYY-MM-DD  hh:mm:ss')
      },
      {
        title: '',
        dataIndex: 'auth_time',
        render:(auth_time)=> moment(auth_time).format('YYYY-MM-DD  hh:mm:ss')
      },
      {
        title: '',
        dataIndex: 'auth_name',
      },
    ];
  }
  /**
   * 
  */
  getRoleList = async () => {
    this.setState({ loading: true });
    const res = await reqRoleList();
    this.setState({ loading: false });
    if (res.status === 0) {
      const roleList = res.data;
      this.setState({ roleList });
    } else {
      message.error('');
    }
  }
  /**
   * 
  */
  addRole = () => {
    this.form.validateFields(async (errors, values) => {
      console.log('', values, errors);
      if (!errors) {
        // 
        this.setState({
          isShowAdd: false
        });
        const { roleName } = values;
        this.form.resetFields();
        const res = await reqAddRole(roleName);
        console.log('xxxx', res);
        if (res.status === 0) {
          message.success('');
          //  roleList 
          // react
          const role = res.data;

          // const roleList = this.state.roleList;
          // roleList.push(role);
          // this.setState({roleList});

          // react
          // roleList
          this.setState(state => ({
            roleList: [...state.roleList, role]
          }))

        } else {
          message.error('');
        }
      }
    })
  }
  /**
  *  
  */
  updateRoleAuth = async () => {
    this.setState({isShowAuth:false})
    // 1
    // 2
    // 3
    // 4
    const menuKeys = this.menuKeys.current.getMenus();
    const role = this.state.role;
    const userinfo = JSON.parse(React.$storage_utils.getUser());
    role.auth_time = Date.now();
    role.auth_name = userinfo.username;
    role.menus = menuKeys;
    // console.log('xxxxxxx',userinfo._id,role._id);
    const res = await reqUpdateRole(role);
    if(res.status === 0) {
      // 
      // 
      if(role._id === userinfo._id) {
        React.$storage_utils.removeUser();
        this.props.history.push('/login');
        message.success('');        
      } else {
       
        /**
         * 
         * role,roleList
         * roleroleList,roleListrolethis.state.roleList
         */
        // 
        if(userinfo.role_id === role._id) {
          // userinfo = {};          
          React.$storage_utils.removeUser();
          this.props.history.replace('/login');
          message.success('');
        } else {
          message.success('');
          this.setState({
            roleList:[...this.state.roleList]
          })
        }
      }
     
    }
  }
  /**
 * Dom
 * render
 */
  componentWillMount() {
    this.initColunms();
  }
  /**
   * DOMajaxsetState
  */
  componentDidMount() {
    this.getRoleList();
  }
  render() {
    const { role, roleList, loading, isShowAdd, isShowAuth } = this.state;
    const cardTit = (
      <div>
        <Button type="primary" style={{ marginRight: 10 }} onClick={() => { this.setState({ isShowAdd: true }) }}></Button>
        <Button type="primary" disabled={!role._id} onClick={() => this.setState({ isShowAuth: true })}></Button>
      </div>
    )
    const rowSelection = {
      type: 'radio',
      hideDefaultSelections: true,
      onSelect: (role) => { // radio
        // console.log('onSelect', role);
        this.setState({ role })
      }
    };
    return (
      <Card type="inner" className="product-index" title={cardTit}>
        <Table
          rowSelection={rowSelection}
          columns={this.columns}
          dataSource={roleList}
          loading={loading}
          bordered={true}
          rowKey='_id'
          pagination={pagations}
        />
        <Modal
          title=""
          okText=""
          cancelText=""
          visible={isShowAdd}
          onOk={this.addRole}
          onCancel={() => {
            this.setState({ isShowAdd: false })
          }}
        >
          {/*  */}
          <AddRole
            setForm={(form) => this.form = form}
          />
        </Modal>
        <Modal
          title=""
          visible={isShowAuth}
          onOk={this.updateRoleAuth}
          onCancel={() => {
            this.setState({ isShowAuth: false })
          }}
        >
          <RoleAuth role={role} ref={this.menuKeys} />
        </Modal>
      </Card>
    );
  }
}

export default Role;