import React from 'react';
import PropType from 'prop-types';
import { Upload, Icon, Modal,message } from 'antd';
import {reqDeleteImg} from './../api';
import {BASE_IMG_URL,UPLOAD_IMG} from './../utils/constants'

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

export default class UploadImg extends React.Component {
  static propTypes = {
    imgs: PropType.array
  }
  constructor(props) {
    super(props);
    
    /**
     * fileList
     */
    let fileList = [];
    const {imgs} = this.props;
    if(imgs && imgs.length > 0) {
      fileList = imgs.map((img,index) => (
        {
          uid: -index, // id
          name: img,
          status:'done',
          url: `${BASE_IMG_URL}${img}`
        }
      ))
    }
    this.state = {
      previewVisible: false, // 
      previewImage: '', //  url
      fileList
    }
  }
  // state = {
  //   previewVisible: false, // 
  //   previewImage: '', //  url
  //   fileList: [
  //     /**{
  //       uid: '-1',
  //       name: 'image.png',
  //       status: 'done', // uploading done error removed
  //       url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
  //     }*/
  //   ],
  // };

  /**
   *  modal
   */
  handleCancel = () => this.setState({ previewVisible: false });

  /**
   * 
   */
  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    this.setState({
      previewImage: file.url || file.preview, // preview base64,
      previewVisible: true,
    });
  };

  /**
   * file:
   * fileList: 
   */
  handleChange = async ({ file,fileList }) => {
    if(file.status === 'done') { //  filenameurl
      const res = file.response;
      if(res.status === 0) {
        message.success('');
        const {name,url} = res;
        
        /**
         * file  fileList[fileList.length-1] file
         * fileList[fileList.length-1]
         */
        file = fileList[fileList.length-1];
        file.name = name;
        file.url = url;
        
      } else {
        message.error('');
      }
    } else if(file.status === 'removed') { // 
      const res = await reqDeleteImg(file.name ? file.name : file.response.data.name);
      console.log('',file);
      if(res.status === 0) {
        message.success('')
      }
    }
    this.setState({ fileList })
  };
  /**
   * 
   */
  getImgs = ()=> {
    console.log('')
    return this.state.fileList.map(file => file.name);
  }
  render() {
    const { previewVisible, previewImage,fileList } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    return (
      <div className="clearfix">
        <Upload
          action={UPLOAD_IMG} /* https://www.mocky.io/v2/5cc8019d300000980a055e76  */
          accept="image/*" /** */
          listType="picture-card"
          name="image" /** */
          fileList={fileList} /* */
          onPreview={this.handlePreview}
          onChange={this.handleChange}
        >
          {fileList.length >= 3 ? null : uploadButton}
        </Upload>
        <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </div>
    );
  }
}
