import React, { Component } from 'react';
import {Redirect} from 'react-router-dom';
import './../../assets/css/login.less'
import { Form, Input, Button,message,notification} from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { reqLogin } from './../../api'
import storageUtils from './../../utils/storageUtils';
import {connect} from 'react-redux'
import {loginFunc} from './../../redux/action'


const Item = Form.Item; // constimport

class Login extends Component {
  state = {
    loading: false
  }
  enterLoading = () => {
    this.setState({
      loading: true
    })
  }
  handleSubmit = e=> {
    e.preventDefault();
    this.props.form.validateFields(async (err,values) => {
      if(!err) {
        let { username,password } = values;
        console.log('',values);
        /**
         *  async  await  promise  .then()
         * ()
         * 1 await ?
         *    - promiseawait,promise,promise
         * 2 async ?
         *    - await()async 
         */
        // try {
        //   const response = await reqLogin({ username,password });
        //   console.log('',response.data);
        // } catch(error) {
        //   console.log('',error);
        // }
        

        //  redux  
        const response = await reqLogin({ username,password });
        this.enterLoading();
        if(response.status === 0) {
          message.success('');
          storageUtils.setUser(JSON.stringify(response.data));
          // goBack() 
          // this.props.history.push('/'); push 
          this.props.history.replace('/'); // 
        } else {
          this.setState({
            loading:false
          })
          message.error(response.msg)
        }
        
        //  redux  
        // this.props.loginFunc(username,password);
      //  console.log('',);    
        
      } else {
        console.log('!')
      }
    })
  }
  // 
  validatePwd = (rule, value, callback)=> {
    // callback() 
    // callback("xxx") 
    if(!value) {
      callback('');
    } else if(value.length < 4) {
      callback('4');      
    } else if(value.length > 12 ) {
      callback('12');
    } else if(!/^[a-zA-Z0-9_]+$/.test(value)) {
      console.log(value);
      callback('');      
    } else { // 
      callback();
    }
  }

  componentDidMount() {
    notification.open({
      message: '',
      duration: null,
      description: ' admin() () '
    })
  }
  componentWillUnmount() {
    notification.destroy();
    this.timer && clearTimeout(this.timer);
  }

  render() {
    // 
    let user = JSON.parse(storageUtils.getUser());
    if(user && user.username) {
      return <Redirect to='/' />;
    }

    // form
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login">
        <div className="loginForm">
          <h2></h2>
          <Form
            name="normal_login"
            className="login-form"
            onSubmit={this.handleSubmit}
          >
            <Item>
              {getFieldDecorator('username', {
                // 
                rules: [
                  { required: true,whitespace:false, message: 'Please input your username!' },
                  { min: 4, message: '4' },
                  { max: 12, message: '12' },
                  {pattern:/^[a-zA-Z0-9_]+$/,message:''}
                ]
              })(
                <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" className="form-input" />
              )}
            </Item>
            <Item>
              {getFieldDecorator('password', {
                rules: [{
                  // 
                  validator: this.validatePwd
                }]
              })(
                <Input prefix={<LockOutlined className="site-form-item-icon" />}
                  type="password"
                  className="form-input"
                  placeholder="Password"
                />
              )}
            </Item>
            <Item
              name="submit-btn"
              className="submitBtn"
            >
              <Button type="primary" htmlType="submit" className="login-form-button"
              loading={this.state.loading}>
                
              </Button>
            </Item>
          </Form>
        </div>
      </div>
    );
  }
}
/**
 * 
 *   1). 
 *        a. 
 *        b.   
 * 
 *   1). 
 *   2). ()()
 *   
 *   3). 
 *   4). 
 *   
 */

/**
 * FormForm(Login)
 * Formform
 */
const WrapLogin = Form.create()(Login);
// export default WrapLogin;

//  redux  UI 

export default connect(
  state => ({userinfo:state.userinfo}),
  {loginFunc}
)(WrapLogin);
