import React, { Component } from 'react';
import { Card, Button, Icon, Table, message, Modal } from 'antd';
import './../../assets/css/category.less'
import { reqCategorys, reqUpdateCategory, reqAddCategory } from './../../api';
import AddCategory from './AddCategory';
import UpdateCategory from './UpdateCategory';
import { pagations } from './../../utils/tablePagination';
class Category extends Component {
  state = {
    categorys: [], // 
    loading: false,
    parentId: '0',
    // selectedId: '', // 
    parentName: '',
    subCategory: [], // 
    showStatus: 0, //  0  1: 2
  }
  // table
  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'name',
      }, {
        title: '',
        width: '25%',
        dataIndex: '',
        render: (category) => <div>
          <span className="link" onClick={() => this.showUpdate(category)}><Icon type="edit" /></span>
          {/*
              onClick={this.showSubCategory}  
              this.showSubCategory(category) 
           */}
          {this.state.parentId === '0' ? <span className='link' onClick={() => this.showSubCategory(category)}><Icon type="edit" /></span> : null}
        </div>,
      },
    ];
  }
  /**
   * ajax
   */
  getCategorys = async (parentId) => {
    this.setState({ loading: true });

    parentId = parentId || this.state.parentId;
    const res = await reqCategorys(parentId);
    console.log('',res);
    this.setState({ loading: false });
    if (res.status === 0) {
      const categorys = res.data;
      if (parentId === '0') { // 
        this.setState({
          categorys
        })
      } else {
        this.setState({
          subCategory: categorys
        })
      }
    } else {
      message.error('');
    }
  }
  // 
  showCategory = () => {
    this.setState({
      parentId: '0',
      subCategory: [],
      parentName: ''
    })
  }
  // 
  showSubCategory = (category) => {
    // setStateparentId
    this.setState({
      parentId: category._id,
      parentName: category.name
    }, () => {
      this.getCategorys();
    })
  }
  /**
   * Dom
   * render
   */
  componentWillMount() {
    this.initColumns();
  }
  /**
   * DOMajaxsetState
   */
  componentDidMount() {
    this.getCategorys();
  }
  /**
   * 
   */
  showAdd = () => {
    this.setState({
      showStatus: 1
    })
  }
  /**
   * 
   */
  showUpdate = (category) => {
    this.edit_category = category;
    this.setState({
      showStatus: 2
    })
  }
  /**
   * 
   */
  addCategory = () => {
    /**
     * 1
     * 2ID
     * 3
    */
    this.form.validateFields(async (errors, values) => {
      if (!errors) {
        // 
        this.setState({
          showStatus: 0
        })
        const { categoryName, parentId } = values;
        const res = await reqAddCategory({ parentId, categoryName });
        // 
        this.form.resetFields()
        if (res.status === 0) {
          // 
          if (parentId === this.state.parentId) {
            // 
            this.getCategorys();
          } else if (parentId === '0') { // 
            this.getCategorys('0');
          }
        }
      }
    })
  }
  /**
   * 
   */
  updateCategory = () => {
    /**
      * 1updateCategory
      * 2
      * 3
    */
    this.form.validateFields(async (errors, values) => {
      if (!errors) {
        const categoryId = this.edit_category._id;
        /**
         * 
         * this.formform
         */
        const categoryName = this.form.getFieldValue('categoryName');
        const res = await reqUpdateCategory({ categoryId, categoryName });
        this.form.resetFields(); // 
        if (res.status === 0) {
          this.getCategorys();
        }
        this.handleCancel();
      }
    })

  }

  handleCancel = e => {
    this.form.resetFields(); // 
    this.setState({
      showStatus: 0
    })
  };


  render() {

    const { categorys, loading, parentId, subCategory, parentName, showStatus } = this.state;
    const edit_category = this.edit_category || {}; // this.edit_category

    this.selectedId = parentId === '0' ? categorys[0] && categorys[0]._id : parentId;

    const left_title = parentId === '0' ? '' : (
      <div>
        <span className="link" onClick={this.showCategory}></span>
        <Icon type="arrow-right" style={{ marginRight: 10, marginLeft: 10 }} />
        <span>{parentName}</span>
      </div>
    );
    const extra = (
      <Button type="primary" onClick={this.showAdd}><Icon type="plus" /></Button>
    )

    return (
      <div className="container category-container">
        <Card title={left_title} extra={extra}>
          <Table rowKey='_id' dataSource={parentId === '0' ? categorys : subCategory} bordered loading={loading} columns={this.columns} pagination={pagations} />
          <Modal
            title=''
            visible={showStatus === 1}
            onOk={this.addCategory}
            onCancel={this.handleCancel}
            cancelText=""
            okText=""
          >
            {/*  (form)=>this.form = form} */}
            <AddCategory categorysList={categorys} setForm={(form) => this.form = form} parentId={parentId} />
          </Modal>
          <Modal
            title=''
            visible={showStatus === 2}
            onOk={this.updateCategory}
            onCancel={this.handleCancel}
          >
            <UpdateCategory categoryName={edit_category.name} setForm={(form) => this.form = form} />
          </Modal>
        </Card>

      </div>
    );
  }
}

export default Category;