import store from 'store';
const LOCAL_USER = "local_user";

/**
 * https://github.com/marcuswestin/store.js
 */

export default {
  /**
   * @param {*} user 
   * 
   */
  setUser(user) {
    store.set(LOCAL_USER, user);
  },
  /**
   * 
   */
  getUser() {
    return store.get(LOCAL_USER) || '{}';
  },
  removeUser() {
    store.remove(LOCAL_USER);
  }
}