import React, { Component } from 'react';
import { Card, Button, Table, message, Modal, Tag } from 'antd';
import moment from 'moment';
import { pagations } from './../../utils/tablePagination';
import {reqUsers,reqDeleteUsers,reqAddOrUpdateUsers} from './../../api';
import UserForm from './UserForm';
class User extends Component {
  state = {
    users: [], // 
    roles:[], // 
    loading:false,
    userEditorModal:false,
  }
  initColumns = () => {
    this.columns = [
      {
        title: '',
        dataIndex: 'username',
      },
      {
        title: '',
        dataIndex: 'email',
      },
      {
        title: '',
        dataIndex: 'phone',
      },
      {
        title: '',
        dataIndex: 'create_time',
        render:(create_time)=>moment(create_time).format('YYYY-MM-DD  hh:mm:ss')
      }, {
        title: '',
        dataIndex: 'role_id',
        render:(role_id) => this.roleNames[role_id]
      },
      {
        title: '',
        key: 'action',
        render: (text, user) => (
          <div>
            <Tag color="#2DAC91" onClick={()=>this.editUser(user)}></Tag>
            <Tag color="#2DAC91" onClick={() => this.delUser(user)}></Tag>
          </div>
        ),
      },
    ];

  }
  /**
   * 
  */
  getUserList = async () => {
    this.setState({loading:true});
    const res = await reqUsers();
    this.setState({loading:false});
    if(res.status === 0) {
      const {users,roles} = res.data;
      this.initRoleName(roles);
      this.setState({users,roles});
    } else {
      message.error('');
    }
  }
  /*
     
  */
  initRoleName = (roles) => {
    // reduceitemindex,
    const roleNames = roles.reduce((pre,role)=> {
      pre[role._id] = role.name
      return pre
    },{})
    this.roleNames = roleNames;
  }
  /**
   * 
  */
  delUser = (user) => {
    const {_id,username} = user;
    Modal.confirm({
      title: `${username}`,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const res = await reqDeleteUsers(_id);
        if(res.status === 0) {
          message.success('');
          this.getUserList();
        }
      }
    });
  }
  /**
   * 
   */
  editUser = (user)=> {
    this.setState({userEditorModal:true});
    this.user = user;
    console.log('',user);
  }
  /**
   * 
   */
  addOrEditUser = ()=> {
    // this.setState()
    /**
     * 1
     * 2
     * 3
     * 4
     */
    this.form.validateFields(async (error,values) => {
      if(!error) {
        if(this.user) {
          values._id = this.user._id;
        }
        const res = await reqAddOrUpdateUsers(values);
        // 
        this.form.resetFields();
        this.setState({userEditorModal: false});
        if(res.status === 0) {
          
          // localStorage
          // 
          let localuser = JSON.parse(React.$storage_utils.getUser());
          console.log('user',this.user,localuser);
          if(localuser._id === this.user._id) {
            React.$storage_utils.removeUser();
            this.props.history.push('/login');
            message.success('');
          } else {
            message.success(`${this.user ? '' : ''}`);
            this.getUserList();
          }

        } else {
          message.error(`${this.user ? '' : ''}`);
        }
      }
    })

    // console.log('xxx',user);
  }
  componentWillMount() {
    this.initColumns();
  }
  componentDidMount() {
    this.getUserList();
  }
  render() {
    const { columns } = this;
    const user = this.user || {};
    const { users,roles,userEditorModal } = this.state;
    const cardTit = (
      <div>
        <Button type="primary" style={{ marginRight: 10 }} onClick={()=> {
          this.user = null; // user
          this.setState({userEditorModal:true})
        }}></Button>
      </div>
    )
    return (
      <Card type="inner" className="product-index" title={cardTit}>
        <Table
          columns={columns}
          dataSource={users}
          bordered={true}
          pagination={pagations}
          rowKey='_id'
        />
        <Modal
          title={user._id ? '' : ''}
          visible={userEditorModal}
          onOk={this.addOrEditUser}
          onCancel={()=> {
            this.form.resetFields()
            this.setState({userEditorModal:false})
          }}
        >
          <UserForm 
            setForm={form => this.form = form}
            roles={roles} 
            user={user} 
          />
        </Modal>
      </Card>
    );
  }
}

export default User;