import React, { Component } from 'react';
import { Card, Icon, Button, Select, Input, Table, Tag,message } from 'antd';
import { reqProducts, reqSearchProducts,reqUpdateStatus } from './../../api'
import { PAGE_SIZE } from './../../utils/constants'
const { Option } = Select;

/**
 * Product
 */
class ProductIndex extends Component {
  state = {
    productList: [],
    total: '',
    loading: false,
    searchName: '', // 
    searchType: 'productName', //  productName:  productDesc:
  }
  // table
  initColumns = () => {
    this.columns = [{
      title: '',
      dataIndex: 'name',
      render: text => <a href="/#">{text}</a>,
    }, {
      title: '',
      dataIndex: 'desc',
    }, {
      title: '',
      width: 100,
      dataIndex: 'price',
      render: (price) => `${price}`
    }, {
      title: '',
      width: 100,
      // dataIndex: 'status',
      render: (product) => {
        const {status,_id,name } = product;
        return (
          <div style={{ textAlign: 'center' }}>
            <Tag color={status === 1 ? '#2DAC91' : '#f00'} onClick={() => this.productStatus(status===1?2:1,_id,name)}>
              {status ===1 ? '' : '' }
            </Tag><br />
            <span style={{ color:(status === 1) ? '#2DAC91' : '#999'}}>{status ===1 ? '' : '' }</span>
          </div>
        )
      },
    }, {
      title: '',
      dataIndex: 'product',
      width: 150,
      render: (text, record) =>
        <div>
          <Tag color='#2DAC91' onClick={() => this.props.history.push('/pro/product/detail', { record })}>
            
          </Tag>
          <Tag color='#2DAC91' onClick={() => this.props.history.push('/pro/product/edit',record)}>
            
          </Tag>
        </div>
    }]
  }
  // 
  getProducts = async (pageNum) => {
    this.pageNum = pageNum;
    this.setState({ loading: true });
    const { searchName, searchType } = this.state
    let res
    if (searchName) {
      res = await reqSearchProducts({ pageSize: PAGE_SIZE, pageNum, searchName, searchType });
    } else {
      res = await reqProducts({ pageSize: PAGE_SIZE, pageNum });
    }
    console.log(res);

    this.setState({ loading: false });
    if (res.status === 0) {
      const { total, list } = res.data;
      this.setState({
        total,
        productList: list
      })
    }
  }
  /**
   * Dom
   * render
   */
  componentWillMount() {
    this.initColumns();
  }
  /**
    * DOMajaxsetState
    */
  componentDidMount() {
    this.getProducts(1);
  }

  /**
   * 
  */
  productStatus = async (status,productId,name) => {
    // const {categoryId,status} = category;
    
    const res = await reqUpdateStatus(productId,status);
    if(res.status === 0) {
      // message.success(`${name}`);
      message.success(``);
      this.getProducts(this.pageNum);
    }
  }
  render() {
    const { productList, total, loading, searchType } = this.state;
    const extra = (
      <Button type="primary" onClick={() => this.props.history.push('/pro/product/edit')}><Icon type="plus" /></Button>
    )
    const cardTit = (
      <div>
        <Select
          value={searchType}
          style={{ width: 150 }}
          onChange={value => this.setState({ searchType: value })}
        >
          <Option value='productName'></Option>
          <Option value='productDesc'></Option>
        </Select>
        <Input
          placeholder=''
          style={{ width: 150, margin: '0 15px' }}
          onChange={event => this.setState({ searchName: event.target.value })}
        />
        <Button type='primary' onClick={() => this.getProducts(1)}></Button>
      </div>
    )
    return (
      <Card type="inner" className="product-index" title={cardTit} extra={extra}>
        <Table
          columns={this.columns}
          dataSource={productList}
          bordered
          loading={loading}
          rowKey='_id'
          pagination={{
            current: this.pageNum,
            total: total,
            defaultCurrent: 1,
            defaultPageSize: PAGE_SIZE,
            onChange: this.getProducts,
            showQuickJumper: true
          }}
        />
      </Card>
    );
  }
}

export default ProductIndex;